.about {
    padding-top: 100px;
    padding-bottom: 130px;
    line-height: 25px;
    color: #616161;
}

.about__explain {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin: 100px auto;
    margin-bottom: 50px;
    width: 100%;
    max-width: 1200px;

}

.about-explain__column {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 270px;
    height: 270px;
    padding: 0 10px;
}

.expain__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 3em;
    background-color: #1FC1C7;
    width: 100px;
    height: 100px;
    border-radius: 50px;

}

.explain__title {
    margin: 15px 0 10px 0;
    font-size:1.7em;
    font-weight: 600;
}

.explain__content {
    text-align: center;
}

.about-profile {
    display: flex;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
}

.about-profile__me {
    display: flex;
    flex: 1 1;
    flex-direction: column;
    align-items: center;
    padding: 0 50px;
}
.about-profile__skills {
    flex: 1 1;
}

.profile-me__img {
    width: 200px;
    height: 200px;
}

.profile-me__title {
    font-size:1.7em;
    font-weight: 600;
    margin: 20px auto;
}

.profile-me__content {
    text-align: center;
}

.profile-me__img img {
    width: 100%;
    height: 100%;
    border-radius: 100px;
}

.about-profile__skill {
    display: flex;
    height: 30px;
    justify-content: space-between;
    background-color: #EDEDED;
    margin-bottom: 10px;

}

.profile-skill__bar {
    width: 75%;
    background-color: #19A0A5;
}

.profile-skill__rate {
    margin-right: 10px;
}

.profile-skill__title {
    text-align: center;
    background-color: #20C1C7;
    color: white;
    height: 100%;
    font-weight: 600;
    width: 100px;
}
.header {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header__underline {
    width: 100px;
    border: 2px solid #47494B;
    margin: 15px auto;
}

.header__title {
    color: #444649;
    font-weight: 600;
    font-size: 2.5em;
}

.header__subtitle {
    font-size: 1.5em;
    font-weight: 500;
}
.home {
    display: flex;
    width: 100%;
    height: 922px;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: black;
}
.home-header__row{
    display: flex;
    flex-direction : column;
    justify-content: center;
    align-items: center;
}
.home-header__title{
    color: #fff;
}
.home-header__title-highlight {
    font-size: 60px;
    color : blueviolet;
}

.home-header__title{
    font-size: 50px;
}

.home-header__btn--over{
    border : 2px solid #fff;
    width: 300px; 
    height : 60px;
    margin-top: 20px;
    font-size: 30px;
    background-color:black;
    color: #fff;
    justify-content: center;
}

.home-header__btn--over:hover{
    border : 2px solid #04C2C9;
    background-color: #04C2C9;
    cursor: pointer; 
}

